import React, { Suspense, lazy } from "react";
import { isTagEmbed, isTagbox } from "../../../../actions/api";
const Video = lazy(() => import( "../../Elements/Media/Video"));
const Image = lazy(() => import( "../../Elements/Media/Image"));
const MediaIcon = lazy(() => import( "../../Elements/MediaIcon/MediaIcon"));
const Media = ({ itemData, wallID, ownerId, wall, isCover, ThemeRule }) => {
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed || wall.User.userDatabaseType == "tagembed" || isTagbox ? ThemeRule.aspectImageRatio ? ThemeRule.aspectImageRatio : 100 : 100
  const mediaIcon = itemData.hotspot ? false : true
  return <div className="tb_hc_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
    {mediaIcon ? <Suspense fallback={<></>}><MediaIcon itemData={itemData} IconClass="tb_hc_media_icon" show={mediaIcon} isCenter={false} /></Suspense> : null}
    {autoPlay ?
      <Suspense fallback={<></>}><Video VideoClass="tb_hc_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} wall={wall} size={size} isCover={isCover} muted={true} autoPlay={autoPlay} handleVideoEnded={() => { }} /></Suspense>
      :
      <Suspense fallback={<></>}><Image ImageClass="tb_hc_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} hotspot={true} /></Suspense>
    }
  </div>
}
export default Media